@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.centering{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.space-between{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

:root {

  --accent-color: rgb(255, 100, 100);
  --bg-gray: #F5F7FA;
  --black-text: rgb(53, 53, 53); 
  --light-gray: #e0e0e0;
  --gray-text: #7C7C7C;
  --blue-text: #589BFF;
  --shadow: 0px 5px 15px -3px rgba(67, 68, 77, 0.05);
  --bold-text: 600;
  --light-border: 0.5px solid var(--light-gray);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color:white;
}
.App {
  min-height: 100%;
  min-width: 100%;
  margin:0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

p{
  color: var(--black-text);
  font-size: 14px;
  margin: 0;
}

p.smallText{
  font-size: 12px;
}

p.grayText{
  color: var(--gray-text);
}

p.blueText{
  color: var(--blue-text);
}


h3{
