  border: solid var(--blue-text) 1px;
  padding: 15px;
  padding-left: 35px;
  padding-right: 35px;
  cursor: pointer;
  border-radius: 4px;
  margin: 20px;
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  color: var(--blue-text);
  font-size: 16px;
  font-weight: 600;
}

.relative-container{
  position: relative;
}

.button:hover {
  background-color:var(--blue-text); /* Green */
  color: white;
}

.button p{
  color: var(--blue-text);
}


