.designPanel{
  width: 100%;
  height: 235px;
  background-image: url(/home/static/media/DesignBanner.fd9503c3.JPG);
  background-size: cover;
}

.designCard{
  width: 430px;
  height: 241.88px;
  box-shadow: var(--shadow);
  background-color: white;
  display: inline-block;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
  border-style:solid;
  border: 0.5px solid var(--light-gray);
  
}

.designCard:before{
  content: '';
  position: absolute;
  width: 430px;
  height: 241.88px;
  z-index: -1;
  background-color: blue;
  box-shadow: 0px 15px 14px -3px rgba(63, 63, 63, 0.31);
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.designCard:hover:before{
  opacity: 1;
}

